/**
  Variables are named after http://chir.ag/projects/name-that-color/
  ATTENTION ATTENTION: Please order this list alphabetically
*/

.option--1Y-JHSfRF7 {
    display: block;
    overflow: hidden;
    min-width: 120px;
    width: 100%;
    padding: 10px 20px;
    text-align: left;
    border: 0;
    font-weight: inherit;
    background-color: transparent;
    color: #112a46;
}

.option--1Y-JHSfRF7:disabled {
        color: #999;
    }

.option--1Y-JHSfRF7:not(:disabled):hover,
        .option--1Y-JHSfRF7:not(:disabled):active,
        .option--1Y-JHSfRF7:not(:disabled):focus {
            cursor: pointer;
            color: #52b6ca;
        }
