/**
  Variables are named after http://chir.ag/projects/name-that-color/
  ATTENTION ATTENTION: Please order this list alphabetically
*/

.ck.ck-toolbar {
        background-color: #ccc;
        border-radius: 3px;
        margin-bottom: 10px;
        padding: 10px;
        height: 40px;
    }

.ck.ck-toolbar .ck.ck-button {
            display: -webkit-box;
            display: flex;
            background-color: transparent;
            border: none;
            border-radius: 2px;
            height: 20px;
            width: 20px;
            padding: 4px;
            margin-right: 5px;
        }

.ck.ck-toolbar .ck.ck-button.ck-on {
                background-color: #999;
            }

.ck.ck-toolbar .ck.ck-button .ck.ck-icon {
                width: 12px;
                height: 12px;
                -webkit-box-pack: center;
                        justify-content: center;
            }

.ck.ck-toolbar .ck.ck-button .ck.ck-button__label {
                display: none;
            }

.ck.ck-toolbar .ck.ck-toolbar__separator {
            width: 20px;
        }

.ck.ck-content {
        background-color: #fff;
        border: 1px solid #ccc;
        border-radius: 3px;
        padding: 10px;
        font-size: 12px;
        line-height: 20px;
    }

.ck.ck-content p {
            margin-top: 0;
        }
